@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  font-family: 'Montserrat', Helvetica, Arial, sans-serif;
}

body {
  background-color: #150b42;
  color: white;
  padding: 50px;
}

.page-title {
  border: 2px solid #ba6791;
  box-shadow: 5px 5px 0 #8d3e66;
  color: #d6e9ba;
  margin: 0 auto 40px;
  padding: 10px;
  text-align: center;
  width: 150px;
}

.page-content {
  margin: 0 auto;
  max-width: 800px;
  width: 90%;
}

.search-form {
  position: relative;
}

.search-input {
  background-color: #150b42;
  border: none;
  border-bottom: 1px solid #ba6791;
  color: gray;
  font-size: 1.2rem;
  width: 100%;
}

.search-input:focus {
  outline: none;
}

.search-icon {
  bottom: 0;
  color: #d6e9ba;
  cursor: pointer;
  font-size: 1.5rem;
  position: absolute;
  right: 0;
  transform: translateY(25px) rotate(30deg);
}

.card-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.card {
  align-content: center;
  border-radius: 5px;
  box-shadow: 0px 0px 10px #ba6791;
  height: 200px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  position: relative;
}

.card p {
  background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%, rgba(237,250,251,0) 100%);
  bottom: 0;
  color: white;
  font-size: 0.8rem;
  left: 0;
  margin: 0;
  padding: 20px 0 10px;
  position: absolute;
  text-align: center;
  width: 100%;
  z-index: 1;
}

